import React, { Component } from 'react'

export class NewsItem extends Component {


    render() {
        let { title, description, imgUrl, newsUrl, author, time, source } = this.props;

        return (
            <div>
                <div className="card" style={{ width: '18rem' }}>
                    <img src={imgUrl ? imgUrl : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRgC-ymQXG9HtYPbhTqRLURWdiZjnYaHFNQKQ&usqp=CAU"} className="card-img-top" alt="img" />
                    <div className="card-body">
                    <span className="badge  bg-success ">{source ?  source : "unknown"}</span>
                        <h5 className="card-title">{title ? title.slice(0, 30) : "No title"}  </h5>
                        <p className="card-text">{description ? description.slice(0, 60) : "No Description"}</p>
                      
                        <p className="card-text"><small className="text-dark">By {author ? author : "Unknown"} at {new Date(time).toGMTString()}</small></p>
                        {/* new Date(time).toGMTString() will format the time. Remember this format */}
                        {/* title, img and description ternary operator will define the value
                        if something goes empty it will show the after colon thing and otherwise slice the
                        text to a minimal size. You should use this slice in news item not in news page
                        remember this */}
                        {/* slice will cut down extra text and make every card same in size */}
                        {/* always slice the title and description here not in the news.js file  */}
                        <a href={newsUrl} target="_blank" rel="noreferrer" className="btn btn-dark">Read more...</a>
                        {/* target="_blank" will open the link into a new tab */}
                    </div>
                </div>
            </div>
        )
    }


}

export default NewsItem


