import './App.css';
import React, { Component } from 'react'
import Navbar from "./components/Navbar"
import News from './components/News';


import {
  BrowserRouter as Router,
  Switch,
  Route,

} from "react-router-dom";



export default class App extends Component {

  pageSize = 12;
  apiKey = process.env.REACT_APP_NEWS_API
  // remember to write REACT_APP when use .env.local file otherwise it won't work
  country = "us"

  render() {
    return (
      <div>
        <Router>

          <Navbar />

          <Switch>
            <Route exact path="/"> <News apiKey={this.apiKey} key="general" pageSize={this.pageSize} category={"general"} country={this.country} /> </Route>
            <Route exact path="/business"> <News apiKey={this.apiKey} key="business" pageSize={this.pageSize} category={"business"} country={this.country} /> </Route>
            <Route exact path="/entertainment">  <News apiKey={this.apiKey} key="entertainment" pageSize={this.pageSize} category={"entertainment"} country={this.country} /> </Route>
            <Route exact path="/health"> <News apiKey={this.apiKey} key="health" pageSize={this.pageSize} category={"health"} country={this.country} /> </Route>
            <Route exact path="/science"> <News apiKey={this.apiKey} key="science" pageSize={this.pageSize} category={"science"} country={this.country} /> </Route>
            <Route exact path="/sports"> <News apiKey={this.apiKey} key="sports" pageSize={this.pageSize} category={"sports"} country={this.country} /> </Route>
            <Route exact path="/technology"> <News apiKey={this.apiKey} key="technology" pageSize={this.pageSize} category={"technology"} country={this.country} /> </Route>

            {/* point to be noted key is for changing the mount otherwise it won't show the category item and use exact will 
mount the path exactly. so you have to use exact and key both for flowless performance */}

          </Switch>


        </Router>
      </div>
    )
  }
}

