import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";


export default class News extends Component {
    static defaultProps = {

        country: "us",
        category: "general",
        apiKey:"none"
    }

    static propsTypes = {

        country: PropTypes.array,
        category: PropTypes.array,
        apiKey:PropTypes.array,
    }

    capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }


    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            loading: false,
            page: 1,
            totalResults: 0,
        }

        document.title = `${this.capitalizeFirstLetter(this.props.category)} | NewsReact`


        //    don't forget to set the page number here. and remember this format.



    }
    async updateNews() {

        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page}&pageSize=${this.props.pageSize}`

        this.setState({ loading: true })
        let data = await fetch(url)
        let newsData = await data.json()
        this.setState({
            articles: newsData.articles,
            totalResults: newsData.totalResults,
            loading: false

        })
    }

    async componentDidMount() {

        this.updateNews()
    }

    // componentDidMount is important I've remove all data and set it in updateNews for refactoring
    // async will wait for the data to load it's important and await is the thing that load and take time


    // this part was the hardest part I've ever done in react.js
    // it was easy but I just missed the page and did't understand where to set the page number. 
    // I hope this will help me in future to improve and be more careful in such things.


    // onPrevious = async () => {

    //     this.setState({

    //         page: this.state.page - 1
    //     })
    //     this.updateNews()
    // }


    // onNext = async () => {
    //     this.setState({
    //         page: this.state.page + 1

    //     })
    //     this.updateNews()
    // }


    fetchMoreData = async () => {
        this.setState({
            page: this.state.page + 1,
        })

        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page}&pageSize=${this.props.pageSize}`

        let data = await fetch(url)
        let newsData = await data.json()
        this.setState({
            articles: this.state.articles.concat(newsData.articles),
            totalResults: newsData.totalResults,


        })

        // remember this format this.state.articles.concat(newsData.articles), it's very important for infinite scrolling



    }



    render() {
        return (
            <div className="container">

                <div className="text-center my-4">
                    <h2> NewsReact | Top {this.capitalizeFirstLetter(this.props.category)} Headlines </h2>

                    {this.state.loading && <Spinner />}


                </div>
                <InfiniteScroll 
                    dataLength={this.state.articles.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.articles.length !== this.state.totalResults}
                    loader={<Spinner />}
                >
                    <div className="row container" >

                        {/* if this.state.loading is not true than show the spinner
              otherwise show the content. remember this syntex below */}

                        {this.state.articles.map((element) => {

                            return <div className="md-4 my-3 col" key={element.url}>
                                {/* dont forget to return after using map */}
                                <NewsItem title={element.title} description={element.description}
                                    imgUrl={element.urlToImage} newsUrl={element.url}
                                    author={element.author} time={element.publishedAt} source={element.source.name} />

                            </div>

                        })}
                        {/* <div className=" my-3 d-flex justify-content-between">
                        <button disabled={this.state.page <= 1} onClick={this.onPrevious} className="btn btn-dark ">&larr; Previous</button>
                        <button disabled={this.state.page + 1 > Math.ceil(this.state.totalResults / this.props.pageSize)} onClick={this.onNext} className="btn btn-dark">Next &rarr;</button>
                    </div> */}


                        {/* // this function will help to to not show the emtpy pages. remember this format.
                  // you need to set the result in the setSate and then call it here using math.ceil */}

                    </div>
                </InfiniteScroll>

            </div>





        )
    }
}
